@import "./mixins";

$pageWidth_desktop: 430px;
$pageHeight_desktop: 250px;
$pageWidth_mobile: 300px;
$pageHeight_mobile: 185px;

html,
body,
#root {
  height: 100%;
  margin: 0;
  background-color: #000000;
  color: white;
}

@keyframes introBackground {
  to {
    opacity: 0;
    z-index: -1;
  }
}
@keyframes introTitle {
  to {
    transform: translate(110px, -170px) scale(0.5);
  }
}
.introBg {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 100%;
  background-color: #000000;
  animation: introBackground 2s 2s forwards;
  z-index: 99;
}

main {
  height: 100%;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;

  .header {
    position: absolute;
    z-index: 100;
    animation: introTitle 2s forwards;
    font-size: 50px;
    font-weight: 600;
    mix-blend-mode: difference;
  }

  .pageList {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    // page position
    .center {
      z-index: 4;
    }
    .side {
      z-index: 3;
    }
    .sideside {
      z-index: 2;
    }

    //pageWrap
    .pageWrap:hover {
      cursor: pointer;
    }
    .pageWrap:hover .pageBg {
      box-shadow: 0 0 50px rgba(255, 255, 255, 0.8);
    }
    // .pageWrap:hover .content div {
    //   // flex-direction: column;
    //   @include mobile {
    //     top: $pageHeight_mobile + 10;
    //   }
    //   top: $pageHeight_desktop + 10;
    // }
    .pageWrap {
      position: absolute;
      display: flex;
      justify-content: center;
      // overflow: hidden;
      .content {
        position: absolute;
        display: flex;
        flex-direction: column;
        // $i: 1;
        // transform: translateY(calc(#{$i} * 10));
        // transition: top 0.5s;
        // top: $pageHeight_desktop;

        .title,
        .desc {
          // @include mobile {
          //   top: $pageHeight_mobile;
          // }
          // top: $pageHeight_desktop;
          // // transition: top 0.5s;
          font-size: x-large;
        }
      }

      .page {
        @include mobile {
          width: $pageWidth_mobile;
          height: $pageHeight_mobile;
        }
        width: $pageWidth_desktop;
        height: $pageHeight_desktop;
        background-color: rgb(18, 18, 18);
        position: relative;
        box-shadow: 0 1px 4px rgba(255, 255, 255, 0.3),
          0 0 40px rgba(255, 255, 255, 0.1) inset;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        overflow: hidden;
        .img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
      .pageBg {
        position: absolute;
        box-shadow: 0 0 30px rgba(255, 255, 255, 0.8);
        top: 20px;
        bottom: 20px;
        left: 0;
        right: 0;
        border-radius: 100px / 10px;
        transition: box-shadow 0.3s;
      }
    }
  }
}
